@import url(https://fonts.googleapis.com/css2?family=Cormorant+Infant:wght@600&family=Pinyon+Script&family=Quicksand:wght@400;700&display=swap);
.navbar {
    width: 100%;
    padding: .5rem 1rem;

    text-align: center;
}

.main-nav {
    margin: 0 1rem;

    font-family: Cormorant Infant;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    text-decoration: none;
    
    color: #EBF5FF !important;
}

.main-nav:hover {
    text-shadow: 0px 0px 20px #CED8E2;
}

.main-nav.active {
    text-shadow: 0px 0px 20px #CED8E2;
    font-style: italic;
}
.footer {
    position: absolute;
    width: 100%;
    padding: 1rem 0;
    
    font-family: Cormorant Infant;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;

    background: linear-gradient(180deg, #000A14 0%, #1E2832 100%);
    color: #CED8E2;
}

.col {
    align-items: center;
    align-content: center;
}

.col-logo {
    display: flex;
    justify-content: right;
    margin-right: 1rem;
}

.footer-logo {
    max-height: 150px;
}

.col-contact {
    padding-top: 2rem;
}

.footer-icon {
    margin-right: 1rem;

    color: #B1BBC5;
}

.footer-icon:hover {
    box-shadow: 0px 0px 20px #CED8E2;
}

@media only screen and (max-width: 640px) {
    .footer {
        font-size: 16px;
    }
}
.home {
    text-align: center;

    background: linear-gradient(180deg, #3B454F 0%, #000A14 90%);
}

img {
    padding: 2% 0;
}

.title {
    position: absolute;
    top: 50%;
    left: 26%;
}

.greeting {
    font-family: Cormorant Infant;
    font-style: normal;
    font-weight: 600;
    font-size: 4vw;

    color: #EBF5FF;
}

.name {
    font-family: Pinyon Script;
    font-style: normal;
    font-weight: normal;
    font-size: 10vw;
    white-space: pre;

    color: #EBF5FF;
    
    text-shadow: 0px 0px 20px #EBF5FF;
}

@media only screen and (max-height: 800px) {
    img {
        max-height: 800px;
    }
}

html {
    background: #1E2832;
}

.page {    
    font-family: Cormorant Infant;
    font-style: normal;
    font-weight: 600;

    text-shadow: 0px 0px 20px #000A14;

    background: #000A14;
    color: #EBF5FF;
}

.page p {
    font-family: Quicksand;
    font-style: normal;
    font-weight: 500;
}

a:hover {
    cursor: pointer;
}

.carousel-caption {
    background: linear-gradient(
        90deg, 
        rgba(0, 0, 0, 0), 
        #000A14, 
        rgba(0, 0, 0, 0));
}

.carousel-caption:hover {
    text-shadow: 0px 0px 30px #EBF5FF;
}

.carousel-img {
    border-radius: 100px;
    max-height: 80vh;
}

.modal {
    color: #000A14;
}

.modal-header{
    background: linear-gradient(90deg, #EBF5FF 20%, #1E2832 100%);
}

.modal-title {
    font-family: Cormorant Infant;
}

.modal-body {
    font-family: Quicksand;
    font-size: 18px;

    background: #EBF5FF;
}

@media only screen and (max-width: 991px) {
    .carousel-caption {
        background: rgb(0, 10, 20, .5);
    }
}
@font-face {
    font-family: Pinyon Script, Garamond, serif;
    src: url('https://fonts.googleapis.com/css2?family=Cormorant+Infant:wght@600&family=Pinyon+Script&family=Quicksand:wght@400;700&display=swap');
}

@font-face {
    font-family: Cormorant Infant, Garamond, serif;
    src: url('https://fonts.googleapis.com/css2?family=Cormorant+Infant:wght@600&family=Pinyon+Script&family=Quicksand:wght@400;700&display=swap');
}

@font-face {
    font-family: Quicksand, Verdana, Geneva, Tahoma, sans-serif;
    src: url('https://fonts.googleapis.com/css2?family=Cormorant+Infant:wght@600&family=Pinyon+Script&family=Quicksand:wght@400;700&display=swap');
}
